body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  outline: none;
  transition: color .2s ease-in-out;
}

.flex {
  display: -webkit-flex;
  display: flex;
}

header {
  display: block;
}

.top-bar {
  background-color: #222222;
  width: 100%;
  position: relative;
  height: 50px;
  color: #a7a7a7;
}

.container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.row {
  margin-left: -15px;
  margin-right: -15px;
}

.top-bar-links {
  line-height: 50px;
  font-size: 12px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.col-sm-6 {
  position: relative;
  min-height: 1px;
  padding-left: 15px;
  padding-right: 15px;
  width: 50%;
  display: -webkit-flex;
  display: flex;
}

.top-bar-links > ul > .top-bar-email {
  padding-right: 20px;
}

.top-bar > ul, .top-bar-links > ul > li {
  display: inline-block;
  position: relative;
}

.top-bar-links > ul:first-child > li i {
  margin-right: 5px;
  color: #7c7c7c;
}

.fas, .fab, .fa {
  display: inline-block;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.top-bar-links .top-bar-email a {
  color: #a7a7a7;
}

.no-padding {
  padding: 0 !important;
}

.footer-soc .soc-icons a, .top-bar .soc-icons a {
  margin-bottom: 0;
  background-color: transparent;
}

.soc-icons {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.soc-icons a {
  margin: 0 3px 6px 0;
  display: inline-block;
  width: 32px;
  height: 32px;
  color: #7a7a7a;
  background-color: #f2f2f2;
  line-height: 32px;
  text-align: center;
  font-size: 13px;
  transition: all .3s ease-in-out;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.navbar {
  margin-bottom: 0;
  border: none;
  min-height: 100px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: all 0.3s ease-in-out;
  z-index: 1200;
}

/* .navigation {

} */

.relative {
  position: relative;
}

.search-wrap {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 20;
}

.search-wrap .form-control {
  height: 100%;
  border: none;
  font-size: 24px;
}

input[type="search"], select, textarea {
  height: 39px;
  border: 1px solid #e0e0e0;
  background-color: transparent;
  width: 100%;
  margin-bottom: 16px;
  padding: 0 16px;
  box-shadow: none;
  border-radius: 0;
  transition: border-color 0.3s ease-in-out;
}

.search-wrap input, .search-wrap input:focus {
  background-color: #fff !important;
  border: none;
}

.navbar-toggle {
  position: relative;
  margin-top: 13px;
  border: none;
  z-index: 50;
  margin-right: 5px;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}

.navbar-toggle .icon-bar {
  display: block;
  width: 18px;
  height: 2px;
  border-radius: 1px;
  background-color: #7a7a7a;
}

.navbar-toggle .icon-bar+.icon-bar {
  margin-top: 4px;
}

.logo-container {
  padding: 15px;
  width: 25%;
}

.logo-wrap {
    width: 100%;
}

.logo-wrap > a {
  height: 100px;
}

.logo {
  width: auto;
  height: 150px;
  transition: height 0.3s ease-in-out;
}

.nav-wrap {
  /* width: 80%; */
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.col-md-9 {
  width: 75%;
}

.right {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.navbar-collapse {
  border: none;
  padding-left: 0;
  padding-right: 0;
  box-shadow: none;
  -webkit-box-shadow: none;
}

.collapse {
  display: none;
}

.navbar-nav {
  margin: 0;
}

.nav > li {
  position: relative;
  display: block;
}

.navbar-nav > li > a {
  padding: 0 20px;
  line-height: 100px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: .11em;
  color: #7a7a7a;
  font-size: 13px;
}

.navbar-nav > li > a:hover {
  color: #000000;
  transition: 170ms ease all;
}

.nav > li > a {
  position: relative;
  display: block;
}

/* <-- media queries --> */


@media (max-width: 767px) {
  .hidden-xs {
    display: none;
  }
}

@media (max-width: 991px) {
  .navbar {
    min-height: 60px;
  }

  .navbar-header {
    height: 60px;
  }

  .logo-container {
    position: absolute;
    top: 0;
  }

  .logo-wrap {
    width: auto;
  }

  .logo {
    height: 100px;
  }

  .nav-wrap {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    border-top: 1px solid #f2f2f2;
  }
}



@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .container {
    width: 970px;
  }

  .navbar-toggle {
    display: none;
  }

  .navbar-static-top .navbar-collapse {
    padding-left: 0;
    padding-right: 0;
  }

  .navbar-collapse {
    width: auto;
    border-top: 0;
    box-shadow: none;
  }

  .navbar-collapse.collapse {
    display: -webkit-flex !important;
    display: flex !important;
    -webkit-align-items: center;
            align-items: center;
    height: auto !important;
    padding-bottom: 0;
    overflow: visible !important;
  }

  .navbar-right {
    display: -webkit-flex !important;
    display: flex !important;
    -webkit-justify-content: flex-end !important;
            justify-content: flex-end !important;
  }

  .navbar-nav > li {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
.jumbo {
  width: 100%;
  height: 500px;
  overflow: hidden;
  position: relative;
}

.jumbo img {
  position: absolute;
  top: -15%;
}
.portfolio {
  background-color: rgba(11, 77, 177, .3);
  width: 100%;
  height: auto;
}

figure {
  margin: 0;
}

.pane {
  margin: 0 auto;
  max-width: 1200px;
}

.window {
  background-color: rgba(0, 0, 0, .7);

}

.window img {
  display: block;
}

.port-element {
  max-width: 100%;
}

@media screen and (min-width: 600px) {
  .pane {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: row;
            flex-direction: row;
  }

  .window {
    width: 50%;
  } 
}

@media screen and (min-width: 1000px) {
  .window {
    width: calc(100% / 3);
  } 
}

@media screen and (min-width: 1200px) {
  .window {
    width: calc(100% / 4);
  } 
}
.about {
  background-color: rgba(77, 77, 177, .3);
  width: 100%;
  height: 500px;
}

.pane {
  margin: 0 auto;
  max-width: 1200px;
}

.frame p {
  display: block;
  width: 100%;
}

@media screen and (min-width: 600px) {

  .frame{
    width: 50%;
  } 

  .frame:last-of-type {
    width: 50%;
  }

}

@media screen and (min-width: 1000px) {
  .frame {
    width: calc(100% / 3);
  } 

  .frame:last-of-type {
    width: 25%;
  }
}

@media screen and (min-width: 1200px) {
  .frame {
    width: calc(100% / 4);
  }
  
  .frame:last-of-type {
    width: 50%;
  }
}
.contact {
  background-color: rgba(177, 77, 11, .3);
  width: 100%;
  height: 500px;
}

.pane {
  margin: 0 auto;
  max-width: 1200px;
}
.footer {
  background-color: rgba(11, 177, 77, .3);
  width: 100%;
  height: 50px;
}

footer .pane {
  display: -webkit-flex;
  display: flex;
  height: inherit;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
